.dialog {
    margin-bottom: 20px;
    display: flex;
}

.dialog a {
    text-decoration: none;
    color: black;
    margin-left: 10px;
}

.dialog a.active {
    color: red;
    font-size: 20px;
    margin-left: 20px;
}

.dialog a:hover {
    margin-left: 20px;
}

.img {
    height: 40px;
    width: 40px;
    border-radius: 20px;
}


