.Massages {
    padding: 10px;

}

.dialogsText {
    margin-bottom: 10px;
    font-size: 25px;
}

.dialogs {
    display: grid;
    grid-template-columns: 2fr 5fr;
    outline: 0px solid green;
}

.dialogItems {
    padding: 10px;
    padding-bottom: 50px;
}

.massages {
    padding: 10px;
    border-left: 2px solid cadetblue;
    padding-left: 40px;
    display: flex;

}





.myMessages div {
    background: #EEEEEE;
    margin-bottom: 50px;
    border: 1px solid darkgray;
    border-radius: 50px;
    padding: 20px;
    width: 150px;
    outline: 0px solid red;
}

.friendsMessages div{
    margin-left: 250px;
    background:#75A0DE;
    margin-top: 50px;
    border: 1px solid darkgray;
    border-radius: 50px;
    padding: 20px;
    width: 150px;
    outline: 0px solid red
}


